import React from "react"
import PropTypes from "prop-types"
import { StaticQuery, graphql } from "gatsby"
import styled from "@emotion/styled"
import Project from "./project-card"


const Container = styled.div`
  text-align: center;
`

const Grid = styled.div`
  margin: 0 auto;
  max-width: 1200px;
  padding: 0 1.0875rem 1rem;
  padding-top: 2em;
  display: grid;
  grid-template-columns: 1fr 1fr  1fr;
  grid-gap: 10px;
  background-color: #fff;
  color: #444;
`

const OuterContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 78vh;
`// Can probably remove the rigid height once it's been populated with project cards

const Description = styled.p`
  padding: 0;
  margin-bottom: 1rem;
  font-size: 1.4rem;
`

const NameHeader = styled.h1`
  font-size: 3.5rem;
  margin-bottom: 0;
  border-left: 3px solid rgba(102, 102, 255, 0.8);
  border-top: 3px solid rgba(102, 102, 255, 0.8);
  padding-top: .1em;
  padding-left: .1em;
  margin-bottom: 0.2em;
`

const Crossed = styled.a`
  pointer-events: none;
  cursor: default;
  text-decoration: line-through;
  color: rgb(102, 102, 255);
  padding: 0;
  margin-bottom: 1rem;
  font-size: 1.4rem;
`

const LandingBio = () => (
  <StaticQuery
    query={graphql`
      query LandingSiteTitleQuery {
        site {
          siteMetadata {
            title
          }
        }
      }
    `}
    render={data => (
      <OuterContainer>
        <Container>
          <NameHeader>{data.site.siteMetadata.title}</NameHeader>
          <Description style={{color: 'rgb(102, 102, 255)'}}>Projects and <Crossed>shitposts</Crossed> blog</Description>
        </Container>

        <Grid>
          <Project title={"Graphic Design Portfolio"} ></Project>
          <Project title={"Hack BI"}></Project>
          <Project title={"VTHacks"}></Project>
          <Project title={"name"}></Project>
          <Project title={"name"}></Project>
          <Project title={"name"}></Project>
          <Project title={"name"}></Project>
          <Project title={"name"}></Project>
          <Project title={"name"}></Project>
        </Grid>
      </OuterContainer>
    )}
  />
)

NameHeader.propTypes = {
  siteTitle: PropTypes.string,
}

NameHeader.defaultProps = {
  siteTitle: ``,
}

export default LandingBio
